.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

html, body {
  height: 100%;
  margin: 0;
}

/* Estilos existentes... */

body {
  background-color: #121111;
  background-image: url(/src/images/541c9b8f476b22e0161293c7d23dde4c51176892r1-871-929_hq.gif);
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
}

#container {
  width: 100%;
  max-width: 500px;
  padding: 20px;
  border: 5px solid #04b0db;
  border-radius: 5px;
  font-family: Arial, sans-serif;
  opacity: 0.8;
  background-color: rgb(190, 190, 190);
  margin: 20px auto;
}

/* Media query para tablets */
@media (max-width: 768px) {
  #container {
    max-width: 80%;
    padding: 15px;
  }
}

/* Media query para dispositivos móviles */
@media (max-width: 480px) {
  #container {
    max-width: 90%;
    padding: 10px;
  }
}

/* Para pantallas aún más pequeñas */
@media (max-width: 400px) {
  #container {
    max-width: 95%; /* Aumenta ligeramente el ancho en pantallas muy pequeñas */
    padding: 15px; /* Reduce el padding para aprovechar mejor el espacio */
  }
}

.heading {
  text-align: center;
  color: #333;
}

.heading-highlight {
  color: #1A8FE3;
}

.form-group {
  margin-bottom: 10px;
}

.form-group label {
  display: block;
  margin-bottom: 5px;
}

.radio-label {
  margin-right: 10px;
}

select, textarea {
  width: 100%;
  padding: 5px;
}

textarea {
  height: 100px;
}

.error-message {
  color: red;
  margin-bottom: 10px;
}

.download-button {
  width: 100%;
  background-color: #1A8FE3;
  color: #fff;
  padding: 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-weight: bold;
  margin-bottom: 10px;
}

.file-upload {
  margin-bottom: 10px;
}

.clear-file-button {
  margin-left: 10px;
  padding: 5px 10px;
  background-color: #f44336;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}